/* ? google fonts */
@import url("https://fonts.googleapis.com/css2?family=Anek+Bangla:wght@400;500;600;700&display=swap");

@tailwind base;
@tailwind components;
@tailwind utilities;

@layer utilities {
  .h-screen {
    height: 100vh;
    height: 100svh;
  }
  .min-h-screen {
    min-height: 100vh;
    min-height: 100svh;
  }
  .max-h-screen {
    max-height: 100vh;
    max-height: 100svh;
  }

  .h-navbar {
    @apply h-[var(--navbar-height,4rem)];
  }

  .mt-navbar {
    @apply mt-[var(--navbar-height,4rem)];
  }
  .pt-navbar {
    @apply pt-[var(--navbar-height,4rem)];
  }
}

@layer components {
  .-z-1 {
    @apply z-[-1];
  }

  .container {
    @apply px-[16px] md:px-[48px] mx-auto max-w-[1528px];
  }
}

html {
  @apply [--navbar-height:3.375rem];
  @apply scroll-smooth;
  @apply scroll-pt-[var(--navbar-height,4rem)];
}
body,
header,
main,
footer {
  @apply relative;
}
body {
  @apply font-serif;
  @apply grid min-h-screen grid-cols-1;
  @apply grid-rows-[1fr_auto];
  /* ? for static header */
  /* @apply grid-rows-[auto_1fr_auto]; */
}
header#navbar {
  @apply fixed top-0 left-0 z-20 w-full;
}
main {
  @apply grid grid-flow-row max-w-[100vw] overflow-hidden;
}
footer {
  @apply z-10;
}

.link {
  @apply text-[0.875rem] font-semibold text-dark hover:text-primary-300 transition-colors uppercase;
}
.navbar {
  @apply w-full h-navbar;
}
.swap {
  @apply relative;
  input[type="checkbox"] {
    @apply absolute inset-0 w-full h-full opacity-0;
  }
  &:has(input[type="checkbox"]) {
    .swap-on {
      @apply hidden;
    }
    .swap-off {
      @apply block;
    }
  }
  &:has(input[type="checkbox"]:checked) {
    .swap-on {
      @apply block;
    }
    .swap-off {
      @apply hidden;
    }
  }
}
