  @apply relative;
  .swap-on {
    @apply absolute inset-0 invisible transition-all opacity-0;
  }
  .swap-off {
    @apply absolute inset-0 visible transition-all opacity-100;
  }
  &:hover {
    .swap-on {
      @apply visible opacity-100;
    }
    .swap-off {
      @apply invisible delay-75 opacity-0;
    }
  }
} */
#navbar {
  @apply transition-all duration-300 bg-white shadow-navbar;
  .navbar {
    @apply flex items-center justify-between w-full;
  }
  &.open {
    @apply h-[15.6rem];
  }
  &.clear:not(.open) {
    @apply bg-transparent shadow-none;
    /* .navbar-start,
    .navbar-end {
      @apply invisible transition-opacity opacity-0;
    } */
  }
}
.dropdown {
  @apply relative;
  .dropdown-content {
    @apply absolute top-[calc(100%_+_34px)] left-0 invisible opacity-0 transition-opacity;
    a {
      @apply text-[0.75rem] font-semibold text-dark hover:text-primary-500 transition-colors;
    }
    .content-link {
      @apply text-[0.875rem];
    }
  }
}
.dropdown.open {
  .dropdown-content {
    @apply visible opacity-100;
  }
}
.mobile-dropdown {
  button {
    @apply w-full flex justify-between items-center text-[2rem] font-bold text-dark transition-colors uppercase;
    span {
      @apply w-6 h-6 transition-transform;
    }
  }
  &-content {
    @apply grid grid-rows-[0fr] overflow-hidden transition-[grid-template-rows];
    a {
      @apply !text-[1.25rem];
    }
    & > div {
      @apply min-h-[0];
    }
  }
  &.open {
    button {
      @apply text-primary-500;
      span {
        @apply transform rotate-90;
      }
    }
    .mobile-dropdown-content {
      @apply grid-rows-[1fr];
    }
  }
}
.logo {
  @apply w-[166px] h-[22px];
}
.navbar-menu {
  @apply absolute inset-0 invisible w-full h-screen bg-white md:hidden;
  a {
    @apply text-dark hover:text-primary-500 transition-colors;
  }
  a:not(.content-link) {
    @apply text-[2rem] font-bold uppercase;
  }
  .content-link {
    @apply font-normal normal-case;
  }
}
#footer {
  @apply bg-dark text-primary-50;
  .footer {
    @apply flex flex-col gap-[64px] md:gap-[44px] py-[32px] md:py-[55px];
  }
  .footer-bar {
    @apply bg-black;
    ul {
      @apply grid grid-cols-2 gap-[5px] md:flex justify-between py-[15px] md:py-[5px];
      li {
        @apply even:text-right;
        a {
          @apply text-[12px] font-medium text-primary-300;
        }
      }
    }
  }
}

#breadcrumbs {
  @apply hidden mt-8 px-7 md:block;
}
.breadcrumbs {
  @apply flex items-center gap-12;
  &__item {
    @apply relative last:after:hidden text-lg text-primary-300 md:hover:text-primary-500 last:text-primary-500 after:absolute after:-right-2.5 after:top-1/2 after:-translate-y-1/2 after:translate-x-full after:text-primary-500 transition-colors;
  }
}
#catalog {
  @apply mt-3;
}
.catalog {
  @apply md:flex md:gap-6;
}
.filters {
  @apply flex-shrink-0 hidden pt-10 basis-56 md:block;
}
.products {
  @apply w-full;
  h1 {
    @apply font-semibold text-center text-black md:text-left;
  }
  &__settings {
    @apply flex justify-between gap-3 text-black;
  }
  &__items {
    @apply grid mt-6 sm:grid-cols-2 lg:grid-cols-3 gap-x-12;
  }
  &__dropdown {
    @apply md:hidden;
  }
}
.sort-by,
.items-count {
  @apply hidden md:block;
}
.sort-by > p,
.items-count > p {
  @apply text-2xl font-medium text-black;
}
.buttons-settings {
  @apply flex flex-col py-5 text-black md:mt-1 md:p-0 md:flex-row md:flex-wrap md:gap-2 bg-primary-100 md:bg-transparent;
  &__button {
    @apply inline-block py-2 px-10 md:px-0 md:py-2.5 w-full text-lg bg-primary-100 md:hover:bg-primary-300 transition-colors;
    &.active {
      @apply bg-primary-300;
    }
  }
}
.sort-by {
  &__button {
    @apply px-6;
  }
}
.items-count {
  &__buttons {
    @apply justify-end;
  }
  &__button {
    @apply px-3.5;
  }
}
.product-card {
  &__image {
    @apply relative block overflow-hidden aspect-[0.76/1];
    img {
      @apply absolute top-0 left-0 object-cover object-center w-full h-full transition-transform hover:scale-105;
    }
  }
  &__footer {
    @apply flex justify-between gap-8 pl-3 mt-2;
  }
  &__info {
    h2 {
      a {
        @apply text-lg font-semibold;
      }
    }
    span {
      @apply inline-block -mt-2 text-lg;
    }
  }
  &__colors {
    @apply flex items-center gap-2;
    li {
      @apply w-8 h-8 cursor-pointer;
    }
    li:first-child {
      @apply bg-black;
    }
    li:last-child {
      @apply bg-primary-500;
    }
  }
  &:hover {
  }
}
.button {
  @apply inline-flex items-center justify-center text-lg font-medium leading-9 text-center text-white uppercase transition-colors px-7 bg-primary-500 md:hover:bg-primary-300;
}
.controls {
  @apply flex flex-col;
  .button {
    @apply mx-auto md:hidden;
  }
  .arrows {
    @apply mt-10 md:mt-0;
  }
  .page-numbers {
    @apply mt-3;
  }
  p {
    @apply mt-3.5 mx-auto text-xs text-center;
  }
}
.arrows {
  @apply flex items-center justify-center gap-5;
  a {
    @apply transition-colors text-primary-300 hover:text-primary-500;
  }
  a:nth-child(1) {
    @apply rotate-180;
  }
}
.page-numbers {
  @apply flex justify-center gap-3 text-lg font-medium text-primary-300;
  span:first-of-type {
    @apply text-primary-500;
  }
  a {
    @apply transition-colors hover:text-primary-500;
  }
}
.dropdown-products {
  @apply relative whitespace-nowrap;
  &__trigger {
    @apply py-2.5 px-5 w-fit text-lg bg-primary-300;
  }
  &__content {
    @apply absolute left-0 z-10 hidden top-full;
  }
}
.sort-dropdown {
  &__content {
    @apply right-0 left-auto;
  }
}
.filter-dropdown {
  &__trigger {
    @apply flex items-center gap-5;
  }
  &__content {
    @apply w-screen max-w-xs px-10 py-4 bg-white border border-primary-300;
  }
}
.link-category {
  cursor: pointer;
  span {
    @apply text-primary-500 md:text-primary-300;
  }
}
.form-checkbox {
  @apply flex items-center gap-2;
  label {
    @apply inline-flex pt-1 text-xs;
  }
  input[type="checkbox"] {
    @apply relative flex-shrink-0 w-4 h-4 mr-4 bg-transparent border border-black rounded-none appearance-none;
    &:checked {
      @apply bg-center bg-no-repeat;
      background-image: url('data:image/svg+xml,%3Csvg viewBox="0 0 13 10" fill="none" xmlns="http://www.w3.org/2000/svg"%3E%3Cpath d="M1 4.29412L5.05263 8L12 1" stroke="%23574D4D" stroke-width="2"/%3E%3C/svg%3E%0A');
    }
  }
  &.big {
    label {
      @apply py-2 text-xl;
    }
    input[type="checkbox"] {
      @apply w-6 h-6 mr-2;
    }
  }
  &.categories-checkbox {
    @apply gap-6;
    label {
      @apply inline-flex items-center py-1 text-lg text-black;
    }
  }
  & label,
  &.big label {
    @apply py-1 text-xs;
  }
  & input[type="checkbox"],
  &.big input[type="checkbox"] {
    @apply mr-2.5 w-4 h-4;
  }
}
.choices {
  @apply flex flex-col gap-6 mt-2 text-lg text-black;
  &-item {
    @apply flex items-center justify-between w-full gap-6 px-3 py-2 opacity-100 cursor-pointer md:py-0 bg-primary-500 md:bg-primary-300;
    span:last-child {
      @apply w-3 h-3 md:text-black;
    }
  }
  &-group {
    @apply flex flex-col gap-1.5;
  }
  &-container {
    @apply text-black;
    @apply mb-6;
    &.is-hidden {
      @apply hidden;
    }
  }
}
.categories-container {
  @apply text-black;
  ul {
    li {
      @apply py-2;
      a {
        @apply relative flex items-center gap-3 text-lg text-black;
        span {
          @apply flex-shrink-0 transition-transform;
        }
        &.active span {
          @apply rotate-90;
        }
      }
      ol {
        @apply hidden pl-6;
        li {
          @apply pb-0;
        }
      }
    }
  }
}
.collections-container {
  @apply mt-4 text-black;
}
.price-container {
  @apply mt-6;
  &__head {
    @apply flex items-center justify-between mb-6;
  }
}
.choices-title,
.categories-title,
.collections-title,
.price-title {
  @apply text-3xl font-semibold;
}
.search-btn {
  @apply flex p-3 text-white transition-colors bg-primary-500 active:bg-primary-300;
}
.range {
  &__slider {
    @apply relative bg-primary-300;
  }
  &__selected {
    @apply absolute h-full bg-primary-500;
  }
  &__input {
    @apply relative;
    input {
      @apply absolute w-full bg-transparent appearance-none pointer-events-none;
      &::-webkit-slider-thumb {
        @apply w-4 h-4 border-none rounded-full appearance-none pointer-events-auto bg-primary-500;
      }
      &::-moz-range-thumb {
        @apply w-4 h-4 border-none rounded-full appearance-none pointer-events-auto bg-primary-500;
      }
    }
  }
  &__price {
    @apply flex items-center justify-between w-full my-2 gap-7;
    label {
      @apply flex items-center gap-1.5 border-b-2 border-primary-500 text-primary-500;
    }
    input {
      @apply bg-transparent text-primary-500;
      &::-webkit-inner-spin-button {
        @apply hidden;
      }
    }
  }
}
#catalog-slider {
  @apply mt-16;
}
@keyframes running-line-1 {
  0% {
    transform: translateX(100%);
  }
  100% {
    transform: translateX(-100%);
  }
}
@keyframes running-line-2 {
  0% {
    transform: translateX(0%);
  }
  100% {
    transform: translateX(-200%);
  }
}

.running-line {
  @apply w-full overflow-hidden bg-white;

  .running-line-container {
    @apply flex flex-row justify-start min-w-full;
    user-select: none;

    span {
      @apply flex flex-row justify-start;
    }
  }
  p {
    @apply inline-flex items-center ml-[35px] md:ml-[122px] text-dark text-[1.5rem] md:text-[2rem] font-bold uppercase whitespace-nowrap py-[10px];
  }
}
.running-line-left .running-line-container span {
  &:first-child {
    animation: 40s linear 0s infinite running-line-1;
  }
  &:last-child {
    animation: 40s linear -20s infinite running-line-2;
  }
}
.running-line-right .running-line-container span {
  &:first-child {
    animation: 40s linear 0s infinite reverse running-line-1;
  }
  &:last-child {
    animation: 40s linear -20s infinite reverse running-line-2;
  }
}
.splide--new {
  @apply max-w-[100vw];
  .splide__arrow {
    @apply bg-transparent opacity-100 w-[2.25rem] h-[2.25rem] md:w-[1.5rem] md:h-[1.5rem] drop-shadow-lg;
    svg {
      @apply fill-white w-[2.25rem] h-[2.25rem] md:w-[1.5rem] md:h-[1.5rem];
    }
  }
  .splide__arrow--prev {
    @apply left-[16px] md:left-[48px];
  }
  .splide__arrow--next {
    @apply right-[16px] md:right-[48px];
  }
  &.splide--best {
    @apply bg-primary-100;
    .splide--head {
      @apply gap-[60px] md:flex items-center container py-[10px];
      h4 {
        @apply text-[2rem] font-medium text-dark w-full text-center md:w-fit;
      }
    }
    .splide__arrows {
      @apply flex items-center gap-[45px];
    }
    .splide__arrow {
      @apply translate-y-0 md:static drop-shadow-lg;
      svg {
        @apply fill-white md:fill-black;
      }
    }
  }
}
.product-card {
  @apply text-black transition-colors;
  figure {
    @apply aspect-[375/585] md:aspect-[460/616] overflow-hidden;
    img {
      @apply object-cover w-full h-full transition-transform;
    }
  }
  &:hover {
    @apply text-primary-500;
    img {
      @apply scale-105;
    }
  }
  .card-body {
    @apply flex justify-between px-[16px];
  }
  .card-body-left {
    @apply flex flex-col py-[5px];
    h6 {
      @apply text-[1.125rem] font-semibold;
    }
    p {
      @apply text-[1.125rem] font-medium;
    }
  }
  .card-body-right {
    @apply flex gap-[9px] items-center;
    span {
      @apply w-[2rem] h-[2rem] flex-shrink-0;
    }
  }
}
.collection-card {
  @apply relative flex items-end justify-center w-full h-full overflow-hidden text-center transition-colors;
  img {
    @apply absolute inset-0 object-cover w-full h-full -z-1;
  }
  h5 {
    @apply text-[2.5rem] font-semibold leading-[3.75rem] text-primary-500 uppercase z-10;
  }
  &-duo {
    @apply p-[16px] aspect-[375/715];
    h5 {
      @apply text-[4rem] md:text-[4.5rem] font-bold;
    }
    &--left {
      @apply md:items-start md:justify-start;
    }
    &--right {
      @apply md:justify-end;
    }
  }
  &:hover {
    @apply bg-primary-300/50;
  }
}
.newsletter {
  @apply flex flex-col md:flex-row md:items-start pt-[15px] md:pt-[48px] pb-[25px] md:pb-[56px];
  &--info {
    @apply flex flex-col max-w-[415px] md:max-w-[242px];
    h5 {
      @apply text-[2rem] font-semibold leading-[3rem] text-dark uppercase;
    }
    p {
      @apply text-[0.75rem] leading-[1rem] text-dark;
    }
  }
  form {
    @apply w-full flex flex-col md:flex-row items-start pt-[14px] gap-[24px] md:gap-0;
    .input-group {
      @apply h-[2.25rem] w-full;
    }
    .btn {
      @apply h-[2.25rem];
    }
  }
}
.input-group {
  @apply flex-1 px-[0.5rem] border border-primary-500;
  input {
    @apply placeholder:text-[#EAEAEA] text-[1.125rem] font-medium leading-[1.125rem];
  }
}
.button {
  @apply inline-block text-lg font-medium leading-9 text-white uppercase transition-colors px-7 bg-primary-500 hover:bg-primary-300;
}
.selector {
  @apply mb-[17px];
  .selector-res {
    @apply text-[1rem] md:text-[1.5rem] font-medium text-black mb-[8px];
  }
  .options {
    @apply flex gap-[10px] text-[1.125rem] text-black flex-wrap;
    span {
      @apply w-[70px] flex-shrink-0 h-[70px] p-[5px] border border-[#EAEAEA] transition-colors;
      div {
        @apply w-full h-full;
      }
      &.active {
        @apply border-dark;
      }
    }
    &.options--horizontal {
      @apply gap-[16px];
      span {
        @apply w-[96px] h-[40px] flex items-center justify-center;
      }
    }
  }
}
.description-info__info {
  @apply space-y-[40px] md:space-y-0;
  h4 {
    @apply w-fit text-[2rem] font-semibold leading-[3rem] text-primary-500 uppercase relative before:content-[''] before:block before:w-full before:h-[2px] before:absolute before:left-0 before:bottom-0 before:bg-primary-500 mb-[40px] md:hidden;
  }
}
.description-info__tabs {
  @apply hidden md:flex gap-[40px] md:gap-[200px];
  button {
    @apply text-[2rem] font-semibold leading-[3rem] text-primary-300 uppercase relative before:content-[''] before:block before:w-full before:h-[2px] before:absolute before:left-0 before:bottom-0 before:bg-transparent;
    &.active {
      @apply text-primary-500 before:bg-primary-500;
    }
  }
}
.description-info__block {
  &:not(.active) {
    @apply md:hidden;
  }
  ul {
    @apply font-normal text-[1.125rem] leading-[1.126rem] text-black w-3/4;
    strong {
      @apply font-normal;
    }
  }
}
.comment {
  @apply flex flex-col gap-[5px];
  h6 {
    @apply flex gap-[0.5rem] text-[1.5rem] font-medium leading-[2rem] text-black;
  }
  span {
    @apply text-primary-500;
  }
}
#post {
  .post__date {
    @apply mt-2 md:sr-only text-primary-500;
  }
}
.post {
  &-date {
    @apply text-lg text-primary-500;
  }
  &__title {
    @apply font-semibold;
  }
  &__content {
    @apply flex flex-col-reverse md:flex-row gap-9 md:gap-16 lg:gap-24;
  }
  &__text {
    @apply text-lg;
  }
  &__image {
    @apply flex-auto relative h-fit aspect-[0.91/1] md:aspect-[0.67/1];
    img {
      @apply absolute top-0 left-0 object-cover object-center w-full h-full;
    }
  }
}
.back-link {
  @apply items-center hidden gap-4 transition-colors md:inline-flex text-primary-500 md:hover:text-primary-300;
  span {
    @apply rotate-180;
  }
}
.fa-solid {
  @apply w-[1em] h-[1em];
}
.about-hero {
  .breadcrumbs {
    @apply hidden mt-8 md:flex;
  }
}
.lookbook {
  .splide__slide {
    @apply relative aspect-[0.75/1];
    img {
      @apply absolute top-0 left-0 object-cover object-center w-full h-full;
    }
  }
  .splide__arrow {
    @apply bg-transparent opacity-100 w-[2.25rem] h-[2.25rem] md:w-[1.5rem] md:h-[1.5rem] drop-shadow-lg;
    svg {
      @apply fill-white w-[2.25rem] h-[2.25rem] md:w-[1.5rem] md:h-[1.5rem];
    }
  }
  .splide__arrow--prev {
    @apply left-[16px] md:left-[48px];
  }
  .splide__arrow--next {
    @apply right-[16px] md:right-[48px];
  }
}
.item-blog {
  &__head {
    figure {
      @apply relative flex-auto aspect-square;
      img {
        @apply absolute top-0 left-0 object-cover object-center w-full h-full;
      }
    }
  }
}
